<template>
  <div class="container">
    <!-- Header -->
    <el-header class="header">
      <p>若播放异常，请更换接口后重试</p>
    </el-header>

    <!-- Main content -->
    <el-container class="main-container">
      <el-main class="main">
        <!-- Search Section -->
        <div class="search-section mt-4">
          <el-input
            v-model="videoUrl"
            style="max-width: 600px"
            placeholder="请输入视频链接或搜索片名"
            class="search-input"
          >
            <template #prepend>
              <el-select v-model="selectedInterface" placeholder="选择解析接口" style="width: 115px; box-sizing: border-box;" class="interface-select">
                <el-option
                  v-for="(url, name) in apiUrls"
                  :key="name"
                  :label="name"
                  :value="name"
                ></el-option>
              </el-select>
            </template>
            <template #append>
              <el-button type="primary" @click="searchVideo" class="search-button">
                <el-icon><Search /></el-icon>
              </el-button>
            </template>
          </el-input>
        </div>

        <!-- Video Display Section -->
        <div class="video-section">
          <div class="video-placeholder" id="video-placeholder">
            <div class="video-title">今天看点啥...</div>
          </div>
        </div>

        <!-- Time Display Section -->
        <div class="time-theme">
          <div class="time" v-html="currentTime"></div>
        </div>

        <!-- VIP Ad Section -->
        <div class="vip-ad">
          <el-button type="default" @click="toggleEdit">数据</el-button>
        </div>

        <!-- Edit Section -->
        <div v-if="showEdit" class="edit-section">
          <el-input
            v-model="jsonData"
            type="textarea"
            :rows="10"
            placeholder="请输入JSON数据"
            class="edit-input"
          ></el-input>
          <el-button type="primary" @click="saveData" class="save-button">保存</el-button>
        </div>

        <!-- Supported Sites Section -->
        <div class="supported-sites">
          <el-image src="tencent.jpg" alt="腾讯视频" class="site-image" />
          <el-image src="xigua.jpg" alt="西瓜视频" class="site-image" />
          <el-image src="mango.jpg" alt="芒果TV" class="site-image" />
          <el-image src="youku.jpg" alt="优酷视频" class="site-image" />
          <el-image src="iqiyi.jpg" alt="爱奇艺" class="site-image" />
          <el-image src="bilibili.jpg" alt="哔哩哔哩" class="site-image" />
          <el-image src="migu.jpg" alt="咪咕视频" class="site-image" />
          <el-image src="sohu.jpg" alt="搜狐视频" class="site-image" />
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { ElMessage, ElButton, ElInput } from 'element-plus'
import Cookies from 'js-cookie'
import { Search } from '@element-plus/icons-vue'

// Initialize apiUrls with some dummy data
const defaultApiUrls = {
}

const apiUrls = ref(defaultApiUrls)
const selectedInterface = ref('api1')
const videoUrl = ref('')
const currentTime = ref('')
const showEdit = ref(false)
const jsonData = ref('')

// Toggle edit section visibility
const toggleEdit = () => {
  showEdit.value = !showEdit.value
}

// Search video
const searchVideo = () => {
  if (videoUrl.value) {
    const apiUrl = apiUrls.value[selectedInterface.value] + encodeURIComponent(videoUrl.value)
    const videoPlaceholder = document.getElementById('video-placeholder')
    videoPlaceholder.innerHTML = `<iframe src="${apiUrl}" frameborder="0" allowfullscreen width="100%" height="100%"></iframe>`
  } else {
    ElMessage.warning('请输入视频链接或搜索片名')
  }
}

// Update time
const updateTime = () => {
  const now = new Date()
  const year = now.getFullYear()
  const month = String(now.getMonth() + 1).padStart(2, '0')
  const day = String(now.getDate()).padStart(2, '0')
  const hours = String(now.getHours()).padStart(2, '0')
  const minutes = String(now.getMinutes()).padStart(2, '0')
  const seconds = String(now.getSeconds()).padStart(2, '0')
  currentTime.value = `${year} 年 ${month} 月 ${day} 日<br>${hours}:${minutes}:${seconds}`
}

// Save data to cookie
const saveData = () => {
  try {
    const parsedData = JSON.parse(jsonData.value)
    // Update apiUrls with new data
    apiUrls.value = { ...apiUrls.value, ...parsedData }
    Cookies.set('jsonData', jsonData.value, { expires: 7 }) // cookie 有效期7天
    showEdit.value = false
  } catch (e) {
    ElMessage.error('请输入有效的JSON数据')
  }
}

// Load data from cookie on mount
onMounted(() => {
  updateTime()
  setInterval(updateTime, 1000)
  
  // Load apiUrls from cookies if available
  const savedData = Cookies.get('jsonData')
  if (savedData) {
    try {
      const parsedData = JSON.parse(savedData)
      apiUrls.value = { ...defaultApiUrls, ...parsedData }
      jsonData.value = savedData
    } catch (e) {
      ElMessage.error('无法加载保存的数据')
    }
  }
})
</script>


